




















































.bonus-container{
	position: absolute;
	top: 0;
	width: calc(100% - 200px);
	z-index: 5;
	background-color: #f3f3f3;
	height: 200%;
}
@media screen and (max-width: 1300px){
	.bonus-container{
		width: 1110px;
		-webkit-transform: translateX(-130px);
		        transform: translateX(-130px);
	}
}
@media screen and (max-width: 1600px){
	.rightbar{
		width: 0;
	}
	.rightbar nav{
		width: 100px;
		height: 100px;
		right: 100%;
		position: absolute;
	}
}
.bonus-header i{
	font-size: 36px;
}
.bonus-header:before{
	display: inline-block;
	content: '';
	position: relative;
	top: -12px;
	width: 50px;
	height: 2px;
	background-color: #d5d5d5;
}
.bonus-header:after{
	display: inline-block;
	content: '';
	position: relative;
	top: -12px;
	width: 50px;
	height: 2px;
	background-color: #d5d5d5;
}
.line{
	position: relative;
	display: inline-block;
	top: 10px;
	width: 50px;
	height: 2px;
	background-color: #d5d5d5;
}
.bonus-header{
	width: 600px;
	margin: 30px auto;
}
.share:before{
	display: inline-block;
	width: 80px;
	content: '';
}
.share span{
	padding: 0 5px;
}
.share span a{
	color: #555555;
}
.share{
	width: 600px;
	margin: 0 auto;
}
.bonus-header span{
	font-size: 36px;
}
.yellow{
	color: #FB7F6E;
}
.text-list{
	width: 1110px;
	margin: 20px auto;
}
.text-list li{
	position: relative;
	display: inline-block;
	width: 342px;
	height: 660px;
	margin: 10px 10px;
	box-shadow: 5px 5px 10px #d5d5d5;
}
.mask{
	opacity: 0;
	position: absolute;
	top: 0;
	z-index: 16;
	background-color: #efefef;
	width: 342px;
	height: 660px;
	color: #ffffff;
}
.mask:hover{
	opacity: 0.8;
}
.button{
	margin: 300px auto;
	width: 100px;
	height: 40px;
	line-height: 40px;
	text-align: center;
	border-radius: 5px;
	background-color: #F5674C;
}
.bonus-text{
	text-align: center;
	margin-top: 280px;
}
.bonus-text-title{
	font-size: 14px;
	font-weight: bold;
}
.bonus-text-content{
	line-height: 20px;
	margin-top: 30px;
	font-size: 12px;
}



