




























.display-transition{
-webkit-transition: all 0.5s;
transition: all 0.5s;
}
.display-leave{
opacity: 0;
-webkit-transform: translateX(50px);
        transform: translateX(50px);
} 
.display-enter{
  opacity: 1;
}
.container{
  height: 100%;
}
.dropdown .active{
  background-color: #e78170;
}
.dropdown .active a{
  color: #ffffff;
}
@media screen and (max-width: 1300px){
  .nav-user li{
      width: 40px;
      height: 30px;
      overflow: hidden;
  }
  .sidebar{
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
    width: 40px;
    overflow: hidden;
  }
  .showbar{
    -webkit-transform: translateX(-130px);
            transform: translateX(-130px);
    -webkit-transition: all 0.5s;
    transition: all 0.5s;
  }
  .sidebar li{
    padding: 20px 0;
  }
  .sidebar li a{
    padding: 0 auto;
    margin:0 auto;
  }
  .sidebar i{
    font-size: 30px;
  }
  .sidebar .fa-mobile{
    margin-right: 8px;
    font-size: 50px;
  }
}



